import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	data() {
		return {
			severList: [
				[{
						name: '我的收藏',
						icon: 'point.png',
						url: '/pages/collect/collect'
					},
					{
						name: '菜篮子',
						icon: 'gouwuche.png',
						url: '/pages/cart/cart'
					}
				],
				[{
						name: '随机选菜',
						icon: 'choujiang.png',
						url: '/pages/lottery/lottery'
					},
					{
						name: '服务与隐私',
						icon: 'addr.png'
					},
					{
						name: '反馈与建议',
						icon: 'kefu.png'
					}
				]
			],
		};
	},
	onLoad() {
		//加载
		// this.init();
	},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			this.logout();
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		//用户点击订单类型
		toOrderType(index) {
			uni.showToast({
				title: this.orderTypeLise[index].name
			});
		},
		//用户点击列表项
		toPage(list_i, li_i) {
			uni.showToast({
					title: this.severList[list_i][li_i].name
				}),
				uni.navigateTo({
					url: this.severList[list_i][li_i].url
				})
		}
	}
}
